<template>
  <div id="uploadImage">
    <div class="main">
      <div class="tt">上传头像</div>
      <el-form :model="form" :rules="rules" style="margin-top:60px;width:60%" ref="form">
        <el-form-item label :label-width="formLabelWidth" prop="Name">
          <div class="card-infor-imgbox">
            当前用户头像：
            <div
              class="dialog-img"
              :class="{'boy-img':(dialogGender=='男性')&&(dialogImg==''),'girl-img':(dialogGender=='女性')&&(dialogImg=='')}"
              @click="changeImg(i)"
            >
              <img v-if="dialogImg != ''" :src="imgHead+dialogImg+ '&fileType=1&platFormType=1'" />
            </div>
          </div>
          <div class="card-infor-tip">请选择一个新照片进行上传。</div>
          <el-upload
            class="upload-img"
            :file-list="fileList"
            :limit="1"
            drag
            action
            list-type="picture"
            :on-change="handlePreview"
            :auto-upload="false"
            :show-file-list="false"
          >
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">
              将文件拖到此处，或
              <em>点击上传</em>
            </div>
            <img v-show="isGetImg" :src="chooseImg" />
            <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button :loading="createLoading1" type="primary" @click="uploadHandler('form')">
          <i class="el-icon-check el-icon--left"></i>保 存
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import http from "../../../../utils/http";
import api from "../../../../utils/api";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      formLabelWidth: "160px",
      imgHead: "",
      dialogGender: "",
      userid: "",
      username: "",
      usertype: "",
      fileList: [],
      selectArr: [],
      isGetImg: false,
      createLoading1: false,
      dialogImg: "",
      chooseImg: "",
      form: {
        name: "",
        User: []
      },
      rules: {}
    };
  },
  computed: {},
  watch: {},
  async mounted() {
    let downloadURL = await api.GetDownloadFileURL();
    this.imgHead = downloadURL + "?filename=";
    let userInfo = http.getUser();
    if (userInfo) {
      this.userid = userInfo.UserID;
      this.username = userInfo.UserName;
      this.usertype = userInfo.UserType;
    }
    this.getdata();
  },
  methods: {
    async getdata() {
      if (this.usertype == 1) {
        const res = await http.get(api.getBasicInfo, {
          uid: this.userid,
          Token: http.getToken()
        });
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          if (res.Data != null && res.Data.length > 0) {
            this.dialogID = res.Data[0].TeacherID;
            this.dialogImg = res.Data[0].TeacherPhoto;
            //  this.dialogNation = res.Data[0].TeacherNativePlace;
            //  this.dialogMartal = res.Data[0].TeacherIDNO;
            //  this.dialogGender = res.Data[0].TeacherIDValid;
            //  this.form.dialogPlitical = res.Data[0].TeacherPolitical;
            this.dialogName = res.Data[0].TeacherName;
          }
        }
      } else {
        const res = await http.get(api.getBasicInfoStu, {
          uid: this.userid,
          Token: http.getToken()
        });
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          this.dialogID = res.Data[0].StudentID;
          this.dialogImg = res.Data[0].StudentPhoto;
          this.dialogName = res.Data[0].StudentName;
        }
      }
    },
    changeImg(i) {
      this.dialogFix = true;
      if (i.isThc) {
        this.isDialogStu = false;
      } else {
        this.isDialogStu = true;
      }
      if (i.TeacherWorkingDate) {
        this.dialogDate = i.TeacherWorkingDate;
      } else {
        this.dialogDate = i.StudentBirth;
      }
    },
    closeHandler() {
      this.dialogFix = false;
      this.chooseImg = "";
      this.isGetImg = false;
      this.file = "";
      this.fileList = [];
    },
    handlePreview(file, fileList) {
      this.isGetImg = true;
      this.chooseImg = file.url;
      this.file = file;
    },
    async uploadHandler() {
      let url = "";
      if (this.usertype == 1) url = api.uploadThcImg;
      else url = api.uploadStuImg;
      let fd = new FormData();
      if (this.file == undefined || this.file == "" || this.file == null) {
        this.$message({
          message: "请选择图片",
          type: "waining"
        });
        return;
      }
      fd.append("stream", this.file.raw);
      fd.append("id", this.dialogID);
      let res = await http.postFormData(url, fd);
      if (res.type) {
        this.$message({
          message: res.message,
          type: res.type
        });
      } else {
        this.$message({
          message: "修改成功",
          type: "success"
        });
        this.$parent.initData();
      }
      this.closeHandler();
    }
  }
};
</script>

<style lang='less'>
#uploadImage {
  text-align: left;
  width: 956px;
  height: 1017px;
  background: rgba(255, 255, 255, 1);
  border-radius: 3px;
  //
  padding: 20px;
  padding-top: 0;
  .tt {
    color: #333;
    font-size: 14px;
    border-bottom: 1px solid #ecf1f7;
    line-height: 50px;
  }
  .main {
    .dialog-footer {
      width: 100%;
      display: block;
      text-align: center;
      margin-top: 36px;
    }
  }
  .card-infor-imgbox {
    margin-top: 28px;
    .dialog-img {
      margin-top: 18px;
      width: 125px;
      height: 125px;
      background-repeat: no-repeat;
      background-size: contain;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .card-infor-tip {
    margin: 18px 0;
  }
  .upload-img {
    .el-upload-dragger {
      width: 225px !important;
      img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
      }
    }
  }
}
</style>


